<template>
  <div>
      <Row>
          <Col span="12" style="padding-right:10px">
            <treeSelect :treeData="treeData" v-model="data" filterable @on-change="getTreeData" showCheckbox></treeSelect>

          </Col>
          <Col span="12" style="padding-right:10px">
            <Select v-model="model11" filterable multiple>
                <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </Col>
      </Row>
  </div>
</template>
<script>
  export default {
    data (){
      return {
        data:[],
        treeData: [
          {
            title:"parent 0",
            expand:true,
            children:[]
          }
        ],
        cityList: [
                    {
                        value: '122',
                        label: '1'
                    },
                    {
                        value: '12',
                        label: '1'
                    },
                    {
                        value: '13',
                        label: '1'
                    },
                    {
                        value: '144',
                        label: '1'
                    },
                    {
                        value: 'Paris',
                        label: '1'
                    },
                    {
                        value: 'Canberra',
                        label: '1'
                    }
                ],
                model11: []
      }
    },
    methods:{
      getTreeData (value){
        console.log(this.data)
        console.log("getTreeValue:",value)
      }
    },
    created (){
      let v = this,
        Arr = [];
      function makeTree (){
        for (let i = 1; i < 20; i++){
          Arr.push({
            title: 'parent 0-' + i,
            expand : true,
            children:[
              {
                 title: 'leaf 0-' + i + "-1"
              },
              {
                title: 'leaf 0-' + i + "-2",
                expand:true,
                children:[
                  {
                    title:"66"
                  }
                ]
              }
            ]
          })
        }
        return true;
      }
      async function addTree (){
        const a = await makeTree();
        if(a){
          v.$set(v.treeData[0],'children',Arr);

        }


      }
      addTree();


    }

  }
</script>



